export default{
	props:{
		price:{
			type:Array,
			default(){
				return []
			}
		}
	},
	computed:{
		all(){
			let len = this.price.length;
			let all = 0;
			for(let i=0;i<len;i++)
			{
				all = all + this.price[i].price
			}
			return all
		}
	}
}
